@import url(https://fonts.googleapis.com/css?family=Geologica);

:root { 
  --light-outline-color: #1b1f3b;
  --light-emphasis-color: #f75c03;
  --light-contrast-color: #5a7684;
  --light-background-color: #c7ede4;
  --light-decorate-color: #a4243b;
}

body {
  margin: 0;
  background-color: var(--light-background-color);
  font-family: "Geologica", 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Martian Mono", source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
